.mw__wcv {
    display: flex;
    background-image: url('../../assets/msh.png');
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    align-items: center;
    justify-content: center;
}

.mw__wcv-text {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-right:10rem;
    flex: 2; 
}

.mw__wcv-text_border {
    background-color: rgba(27, 78, 122, 0.452); 
    border-radius: 5%;
    box-shadow: 5px 5px 10px;
}

.mw__wcv-title {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 2rem 0;
    padding: 2rem 2rem 0 2rem;
}

.mw__wcv-title h1 {
    width: fit-content;
    background:-webkit-linear-gradient(360deg,#f77171, #fac692);
    -webkit-background-clip: text;
    background-clip: text;
    -moz-background-clip: text;
    -webkit-text-fill-color: transparent; 
    -moz-text-fill-color: transparent;
    font-family: var(--font-family);
    font-size: 48px;
    
    
    
}

.mw__wcv-content {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex: 1;
}

.mw__wcv-bio_container {
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 800px;
    
}

.mw__wcv-bio_container p {
    
    color: #ffffff;
    font-weight: 600;
    font-size: 20px;
    font-family: var(--font-family);
    padding: 2rem;
  
}

.mw__wcv-img_container {
    display: flex;
    margin: 12rem 4rem 0;
    justify-content: right;
    
    max-width: 495px;
    max-height: 600px;
    min-width: 200px;
    
}

.mw__wcv-img_container img {
    height: 100%;
    width: 80%;
    
}

.gradient__bg3 {

    /* ff 3.6+ */
    background:-moz-linear-gradient(343deg, rgba(74, 179, 255, 1) 0%, rgba(0, 12, 80, 1) 37%, rgba(0, 8, 30, 1) 86%); 

    /* safari 5.1+,chrome 10+ */
    background:-webkit-linear-gradient(343deg, rgba(74, 179, 255, 1) 0%, rgba(0, 12, 80, 1) 37%, rgba(0, 8, 30, 1) 86%);

    /* opera 11.10+ */
    background:-o-linear-gradient(343deg, rgba(74, 179, 255, 1) 0%, rgba(0, 12, 80, 1) 37%, rgba(0, 8, 30, 1) 86%);

    /* ie 6-9 */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#4AB3FF', endColorstr='#00081E', GradientType=1 );

    /* ie 10+ */
    background:-ms-linear-gradient(343deg, rgba(74, 179, 255, 1) 0%, rgba(0, 12, 80, 1) 37%, rgba(0, 8, 30, 1) 86%);

    /* global 94%+ browsers support */
    background:linear-gradient(343deg, rgba(74, 179, 255, 1) 0%, rgba(0, 12, 80, 1) 37%, rgba(0, 8, 30, 1) 86%);

}

@media screen and (max-width: 1175px) {
    .mw__wcv-bio_container p {
        font-size: 16px;
    }

    .mw__wcv-text h1 {
        
        font-size: 40px;
    }
}

@media screen and (max-width: 1110px) {
    .mw__wcv-bio_container p {
        font-size: 16px;
    }

    .mw__wcv-text h1 {
        font-size: 36px;
    }
}

@media screen and (max-width: 1045px) {
    .mw__wcv-img_container {
        margin: 12rem 0rem 0;
    }
    .mw__wcv-text {
        margin-right:10rem;
    }
}

@media screen and (max-width: 920px) {
    .mw__wcv-img_container {

        max-width: 350px;
        max-height: 450px;
        min-width: 200px;
    }

    .mw__wcv-title {
        margin: 0rem 0;
        padding: 2rem 2rem 0 2rem;
    }
    .mw__wcv-text {
        margin-right:8rem;
    }
    
}

@media screen and (max-width: 860px) {
    .mw__wcv-img_container {
        max-width: 300px;
        max-height: 400px;
        min-width: 200px;
    }
    .mw__wcv-bio_container p {
        font-size: 14px;
    }

    .mw__wcv-text h1 {
        font-size: 30px;
    }
    .mw__wcv-text {
        margin-right:6rem;
    }
}

@media screen and (max-width: 768px) {
    .mw__wcv-bio_container p {
        font-size: 12px; 
    }

    .mw__wcv-title h1 {
        font-size: 26px;
    }

    .mw__wcv-text {
        margin-right:6rem;
    }
}


@media screen and (max-width: 700px) {
    .mw__wcv-img_container {
        margin: 8rem 0rem 0;
    }
    .mw__wcv-bio_container p {
        font-size: 12px;
    }

    .mw__wcv-text h1 {
        font-size: 26px;
    }  
    .mw__wcv-img_container {
        max-width: 250px;
        max-height: 350px;
        min-width: 200px;
    }
}

@media screen and (max-width: 650px) {
    .mw__wcv-img_container {
        margin: 6rem 0rem 0;
    }
    .mw__wcv-bio_container p {
        font-size: 10px;
    }

   
}

@media screen and (max-width: 620px) {
    .mw__wcv-img_container {
        margin: 4rem 0rem 0;
    }
    .mw__wcv-bio_container p {
        font-size: 9px;
    }

    .mw__wcv-text h1 {
        font-size: 22px;
    }  
}